import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { IPizzaBlock } from '../models';
import { addItem } from '../redux/cart/slice';
import { AppDispatch, RootState } from '../redux/store/store';

const typeNames = ['тонкое', 'традицинное'];
const sizeValues = [26, 30, 40];

type PizzaBlockProps = {
  pizza: IPizzaBlock;
};

export const PizzaBlock: React.FC<PizzaBlockProps> = ({ pizza }) => {
  const cartItem = useSelector((state: RootState) =>
    state.cart.items.find((item) => item.id === pizza.id),
  );
  const addedCount = cartItem ? cartItem.count : 0;
  const [activeSize, setActiveSize] = useState(0);
  const [activeType, setActiveType] = useState(0);
  const dispatch = useDispatch<AppDispatch>();

  const onClickAdd = () => {
    const newItem = {
      id: pizza.id,
      imageUrl: pizza.imageUrl,
      title: pizza.title,
      price: pizza.price,
      type: typeNames[activeType],
      size: sizeValues[activeSize],
    };
    dispatch(addItem(newItem));
  };

  return (
    <div className="pizza-block">
      <Link to={`/pizza/${pizza.id}`}>
        <img className="pizza-block__image" src={pizza.imageUrl} alt={pizza.title} />
        <h4 className="pizza-block__title">{pizza.title}</h4>
      </Link>
      <div className="pizza-block__selector">
        <ul>
          {pizza.types.map((type, index) => (
            <li
              key={index}
              className={activeType === index ? 'active' : ''}
              onClick={() => setActiveType(index)}>
              {typeNames[type]}
            </li>
          ))}
        </ul>
        <ul>
          {pizza.sizes.map((size, index) => (
            <li
              key={index}
              className={activeSize === index ? 'active' : ''}
              onClick={() => setActiveSize(index)}>
              {size} см.
            </li>
          ))}
        </ul>
      </div>
      <div className="pizza-block__bottom">
        <div className="pizza-block__price">от {pizza.price} ₽</div>
        <button onClick={onClickAdd} className="button button--outline button--add">
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <path
              d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
              fill="white"
            />
          </svg>
          <span>Добавить</span>
          {addedCount > 0 && <i>{addedCount}</i>}
        </button>
      </div>
    </div>
  );
};
